* {
  margin: 0;
  padding: 0;
  color: #f5f5f5;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body {
  background-color: #12141d;
  color: #f5f5f5;
}

.app {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.app-content {
  margin-left: 250px;
  width: calc(100% - 250px);
}

@media screen and (max-width: 768px) {
  .app-content {
    margin: auto;
    width: 95%;
  }
}